@import "main";


header {
    display: block;
    top: 0;
    position: fixed;
    right: 0;
    left: 0;
    padding: 60px;
    border: 1px black;
    z-index: 1000;
    background: black;
    color: white;
};

content {
    display: block;
    position: relative;
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    padding-top: 160px;
    border: 1px black;
};